/********************/
.page-header {
  position: sticky; /* Allocates space for the element, but moves it with you when you scroll */
  top: 0; /* specifies the start position for the sticky behavior - 0 is pretty common */
  z-index: 2;
  height: 8em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Overlay visibility while open */
body.menu-open .site-overlay {
  display: block;
  opacity: 1;
}

.page-header button.menu-btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0;
  color: #ffffff;
  border: 0px solid #ccc;
  width: 2.5em;
  height: 2.5em;
  font-size: 2em;
  padding: 0;
  z-index: 2;
}

.page-header button.menu-btn:hover {
  background-color: rgba(150, 140, 120, 0.75);
}

.page-header button.menu-btn:active {
  background-color: #968c78;
}

.page-header .block.menu--main {
  display: none;
}

.page-content {
  /* all page content starts here */
}

.region.region-content {
  padding-top: 2em;
  padding-bottom: 2em;
}

.utility-belt {
  padding-top: 0.6em;
  padding-bottom: 0.3em;
  margin-right: -1.568em;
  margin-left: -1.568em;
  padding-right: 1.568em;
  padding-left: 1.568em;
  display: none; /* hide on handheld */
}

.mgr-links {
  display: flex;
  flex-direction: column;
}

.mgr-links {
  /* display: none;  hide this on mobile */
}

.region.region-actions {
