.body-style p,
.body-style blockquote,
.body-style figure,
.body-style ul,
.body-style ol {
  margin-bottom: 2em;
  font-family: "Open Sans", sans-serif;
  line-height: 1.35em;
  font-size: 1.7em;
  /* max-width: 40rem; use max-width to improve line-length/readability sitewide */
}
.body-style ul,
.body-style ol {
  /* remove margin-bottom from nested lists */
}
.body-style ul ul,
.body-style ul ol,
.body-style ol ul,
.body-style ol ol {
  margin-bottom: 0;
}
.body-style ul,
.body-style ol {
  list-style-position: outside;
  margin-left: 0;
  padding-left: 1.5em;
  overflow: hidden; /* prevent lists from extending into floats */
}
.body-style ul li,
.body-style ol li {
  padding-left: 0.5em;
  margin-bottom: 2px;
}
.body-style ul li:last-child,
.body-style ol li:last-child {
  margin-bottom: 0;
}
.body-style ul ul {
  list-style-type: circle;
}
.body-style ul ul ul {
  list-style-type: square;
}
.body-style ul ul ul ul {
  list-style-type: circle;
}
.body-style li img,
.body-style p img {
  /* display inline images within text */
  vertical-align: middle;
  border: none;
  margin: 0 0.5em;
}
.body-style figure {
  margin: 0;
  margin-bottom: 1em;
  width: 100%;
}
@media all and (min-width: 768px) {
  .body-style figure figure[class^=display--main_66] {
    max-width: 65%;
  }
  .body-style figure figure[class^=display--main_50] {
    max-width: 49%;
  }
  .body-style figure figure[class^=display--main_33] {
    max-width: 32%;
  }
  .body-style figure figure[class^=display--main_25] {
    max-width: 24%;
  }
}
.body-style figure p {
  display: inline; /* remove spacing from paragraph tags in media items */
  margin-bottom: 0;
}
.body-style figure div {
  display: inline;
}
.body-style figure img {
  padding: 0;
  margin: 0; /* remove margin from img in figure tag */
  display: block;
}
@media all and (min-width: 768px) {
  .body-style figure img {
    border-bottom: 0;
  }
}
.body-style figure img.align-center img {
  margin: 0 auto; /* use margin to center img in figure tag */
}
.body-style figcaption {
  color: #959595;
  outline-offset: -1px;
  background-color: unset;
  margin: 0 !important;
  padding: 2em 0 0 0 !important;
  font-size: 0.75em;
  font-style: italic;
}
.body-style figure .media {
  /* when media is inside a figure, remove margins, don't align and fill width */
  float: none;
  width: 100%;
  max-width: 100%;
  margin: 0 !important;
}
.body-style figure .media img {
  display: block;
  width: 100%;
}
.body-style .align-right,
.body-style .align-left,
.body-style .align-center {
  float: none; /* do not float on mobile */
  margin-bottom: 1em;
}
.body-style .align-right p,
.body-style .align-left p,
.body-style .align-center p {
  /* render p as inline */
  display: inline;
}
@media all and (min-width: 768px) {
  .body-style .align-right {
    float: right;
    margin-left: 2em;
    max-width: 65%;
  }
  .body-style .align-right img.inline-img {
    width: 100% !important; /* make aligned image fit figure */
  }
}
@media all and (min-width: 768px) {
  .body-style .align-left {
    float: left;
    margin: 0 2em 0 0;
    max-width: 65%;
  }
  .body-style .align-left img.inline-img {
    width: 100% !important; /* make aligned image fit figure */
  }
}
.body-style .align-center {
  text-align: center;
  margin: 0 auto 1em auto;
}
@media all and (min-width: 768px) {
  .body-style .align-center {
    margin: 0 auto;
    max-width: 95%;
  }
}
@media all and (min-width: 768px) {
  .body-style figure:has([class*=" display--main_100"]) {
    max-width: 100%;
  }
  .body-style figure:has([class*=" display--main_66"]) {
    max-width: 65%;
  }
  .body-style figure:has([class*=" display--main_50"]) {
    max-width: 49%;
  }
  .body-style figure:has([class*=" display--main_33"]) {
    max-width: 32%;
  }
  .body-style figure:has([class*=" display--main_25"]) {
    max-width: 24%;
  }
}
.body-style span.highlight {
  background-color: yellow;
  display: inline-block;
}
@media all and (min-width: 768px) {
  .body-style [class*=" display--main_66"],
  .body-style [data-view-mode*=" main_66"] {
    max-width: 65%;
  }
  .body-style [class*=" display--main_50"],
  .body-style [data-view-mode*=" main_50"] {
    max-width: 49%;
  }
  .body-style [class*=" display--main_33"],
  .body-style [data-view-mode*=" main_33"] {
    max-width: 32%;
  }
  .body-style [class*=" display--main_25"],
  .body-style [data-view-mode*=" main_25"] {
    max-width: 24%;
  }
}
.body-style .media-ext--svg {
  width: 100%; /* on handheld svg media is 100% width */
}
.body-style .media-ext--svg img {
  width: 100%;
}
@media all and (min-width: 768px) {
  .body-style .media-ext--svg[class*=display--main_100] {
    width: 100%;
  }
  .body-style .media-ext--svg[class*=display--main_66] {
    width: 65%;
  }
  .body-style .media-ext--svg[class*=display--main_50] {
    width: 49%;
  }
  .body-style .media-ext--svg[class*=display--main_33] {
    width: 32%;
  }
  .body-style .media-ext--svg[class*=display--main_25] {
    width: 24%;
  }
  .body-style .media-ext--svg img {
    width: 100%;
  }
}
.body-style blockquote {
  margin: 0 1em 2em 0;
  padding: 2em 6em 2em 3em;
  border-left: 6px solid #ccc !important;
}
.body-style blockquote::before {
  display: none; /* hide gin border */
}
.body-style blockquote p {
  margin-top: 0;
  font-weight: 100;
  font-style: italic;
}
.body-style blockquote p:last-child {
  margin-bottom: 0;
}
.body-style blockquote p strong {
  font-weight: 300; /* 500 */
}
.body-style .ck-horizontal-line hr,
.body-style hr {
  height: 0px;
  border-top: 2px solid #959595;
  clear: both;
}
.body-style .text-align-left {
  text-align: left;
}
.body-style .text-align-center {
  text-align: center;
}
.body-style .text-align-right {
  text-align: right;
}
.body-style .text-align-justify {
  text-align: justify;
}
.body-style a.button--red {
  background-color: #d83933;
  color: #fff;
}
.body-style a.button--red:hover {
  background-color: #b50909;
}
.body-style a.button--red:active {
  background-color: #8b0a03;
}
.body-style a.button--orange {
  color: #1b1b1b;
  background-color: #fa9441;
}
.body-style a.button--orange:hover {
  color: #fff;
  background-color: #c05600;
}
.body-style a.button--orange:active {
  color: #fff;
  background-color: #775540;
}
.body-style table {
  border: 1px double #b3b3b3;
  border-collapse: collapse;
  border-spacing: 0;
  height: 100%;
  width: 100%;
  margin-bottom: 3em;
}
.body-style table th,
.body-style table td {
  border: 1px double #b3b3b3;
  padding: 0.4em;
  min-width: 2em;
}
.body-style table th {
  background-color: rgba(0, 0, 0, 0.05);
  font-weight: 700;
}
.body-style table td {
  border: 1px double #b3b3b3;
  padding: 0.25em;
}